"use client";

import { XIcon } from "lucide-react";
import React from "react";
import { Button } from "../ui/button";
import { Card, CardContent } from "../ui/card";

interface LessonModalProps {
  isOpen: boolean;
  onClose: () => void;
  lesson: {
    id: number;
    name: string;
    status: "locked" | "in_progress" | "completed" | "available";
    description: string;
    quizCount: number;
    duration: string;
    questionCount: number;
  } | null;
}

export const LessonModal: React.FC<LessonModalProps> = ({
  isOpen,
  onClose,
  lesson,
}) => {
  if (!isOpen || !lesson) return null;

  const getStatusText = () => {
    switch (lesson.status) {
      case "locked":
        return "Locked";
      case "in_progress":
        return "In Progress";
      case "completed":
        return "Completed";
      default:
        return "Available";
    }
  };

  const getButtonText = () => {
    switch (lesson.status) {
      case "completed":
        return "Review Lesson";
      case "in_progress":
        return "Continue Lesson";
      case "locked":
        return "Unlock Lesson";
      default:
        return "Start Lesson";
    }
  };

  return (
    <>
      <div
        className="fixed inset-0 bg-black/50 z-40 animate-fade-in"
        onClick={onClose}
      />
      <div className="fixed inset-0 flex items-center justify-center z-50 pointer-events-none">
        <Card className="w-full max-w-md mx-4 pointer-events-auto animate-fade-up bg-white rounded-xl border border-[#e6e6e6] shadow-2xl">
          <CardContent className="p-6">
            <div className="flex flex-col gap-4">
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <h3 className="font-semibold-lg font-[number:var(--semibold-lg-font-weight)] text-blackmain-text-color text-[length:var(--semibold-lg-font-size)] tracking-[var(--semibold-lg-letter-spacing)] leading-[var(--semibold-lg-line-height)] [font-style:var(--semibold-lg-font-style)]">
                    {lesson.name}
                  </h3>
                  <div
                    className={`inline-block mt-2 px-3 py-1 rounded-full text-xs font-semibold ${
                      lesson.status === "locked"
                        ? "bg-black-100 text-black-600"
                        : lesson.status === "in_progress"
                          ? "bg-primary-colorprimary/10 text-primary-colorprimary"
                          : lesson.status === "completed"
                            ? "bg-green-100 text-green-700"
                            : "bg-secondary-50 text-secondarysecondary"
                    }`}
                  >
                    {getStatusText()}
                  </div>
                </div>
                <button
                  onClick={onClose}
                  className="p-1 hover:bg-black-50 rounded-full transition-colors"
                >
                  <XIcon className="w-5 h-5 text-black-400" />
                </button>
              </div>

              <p className="font-pharaghraph-sm font-[number:var(--pharaghraph-sm-font-weight)] text-black-400 text-[length:var(--pharaghraph-sm-font-size)] tracking-[var(--pharaghraph-sm-letter-spacing)] leading-[var(--pharaghraph-sm-line-height)] [font-style:var(--pharaghraph-sm-font-style)]">
                {lesson.description}
              </p>

              <img
                className="w-full h-px object-cover"
                alt="Divider"
                src="/devider.svg"
              />

              <div className="flex flex-col gap-2">
                <div className="flex items-center gap-2">
                  <img
                    className="w-5 h-5"
                    alt="Task icon"
                    src="/vuesax-outline-task-square.svg"
                  />
                  <span className="font-body-base font-[number:var(--body-base-font-weight)] text-secondarysecondary text-[length:var(--body-base-font-size)] tracking-[var(--body-base-letter-spacing)] leading-[var(--body-base-line-height)] [font-style:var(--body-base-font-style)]">
                    {lesson.quizCount} Quizzes
                  </span>
                </div>

                <div className="flex items-center gap-2">
                  <img
                    className="w-5 h-5"
                    alt="Clock icon"
                    src="/vuesax-outline-clock.svg"
                  />
                  <span className="font-body-base font-[number:var(--body-base-font-weight)] text-secondarysecondary text-[length:var(--body-base-font-size)] tracking-[var(--body-base-letter-spacing)] leading-[var(--body-base-line-height)] [font-style:var(--body-base-font-style)]">
                    {lesson.duration}
                  </span>
                </div>

                <div className="flex items-center gap-2">
                  <img
                    className="w-5 h-5"
                    alt="Clipboard icon"
                    src="/vuesax-outline-clipboard-text.svg"
                  />
                  <span className="font-body-base font-[number:var(--body-base-font-weight)] text-secondarysecondary text-[length:var(--body-base-font-size)] tracking-[var(--body-base-letter-spacing)] leading-[var(--body-base-line-height)] [font-style:var(--body-base-font-style)]">
                    {lesson.questionCount} Questions
                  </span>
                </div>
              </div>

              <Button
                className="w-full h-12 bg-primary-colorprimary hover:bg-primary-color400 rounded-lg mt-2 disabled:opacity-50 disabled:cursor-not-allowed"
                disabled={lesson.status === "locked"}
              >
                <span className="text-white [font-family:'Montserrat',Helvetica] font-bold text-sm text-center tracking-[0] leading-[21px]">
                  {getButtonText()}
                </span>
                {lesson.status !== "locked" && (
                  <img
                    className="w-5 h-5 ml-1"
                    alt="Arrow right"
                    src="/vuesax-outline-arrow-right.svg"
                  />
                )}
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </>
  );
};
